import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ContactUsServiceService } from 'src/app/core/services/contact-us-service.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-newcontactpage',
  templateUrl: './newcontactpage.component.html',
  styleUrls: ['./newcontactpage.component.scss']
})
export class NewcontactpageComponent implements OnInit {
  public feedbackForm: any;
  currentRating = 3;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  submitted: boolean;

  constructor(private formBuilder: FormBuilder,
    private ContactUsServiceService: ContactUsServiceService,
    private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private router : Router
  ) { }

  ngOnInit(): void {
    this.feedbackForm = this.formBuilder.group({
      describtion: [""],
      rating: [],
    })
  }
  get f() {
    return this.feedbackForm.controls;
  }

  SaveContact() {
    console.log(this.feedbackForm.controls.rating.value)
    this.submitted = true;
    if (this.feedbackForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      // this.toastService.openErrorSnackBar("Please fill mandatory fields!!!");
      return false;
    }
    else {
      let body =
      {
        "enquiry": {
          "ticketType": "FEEDBACK",
          "describtion": this.feedbackForm.controls.describtion.value,
          "rating": this.feedbackForm.controls.rating.value
        },

        "enquiryImages": [],
      }
      this.ContactUsServiceService.contactFeedback(body, this.currentUser.id).then((res) => {
        if (res.success) {
          this.toastService.toastMsg({
            title: "Success",
            content: "Feedback Added Successfully!!!"
          });
          // this.toastService.openSnackBar("Feedback added successfully !!!");
          this.router.navigate(['pages/dashboard'])
          this.resetForm();
        }

      },
        (err) => {
          if (err.error.expose) {
            this.toastService.toastMsg({
              title: "Error",
              content: this.titleCasePipe.transform(err.error.error_message),
            });
            // this.toastService.openErrorSnackBar(
            //   this.titleCasePipe.transform(err.error.error_message)
            // );
          } else {
            this.toastService.toastMsg({
              title: "Error",
              content: "Something Went Wrong.",
            });
            // this.toastService.openErrorSnackBar("Something Went Wrong.");
          }
        });
    }
}

  resetForm() {
    this.feedbackForm = this.formBuilder.group({
      describtion: [""],
      rating: [],
    });
  }
}
